.list {
  display: flex;
  list-style: none;
}
.button {
  width: 200px;
  height: 50px;
  margin-right: 10px;
  background-color: rgb(154, 154, 247);
  color: white;
  font-size: 24px;
  font-weight: bold;
}
.button:hover {
  cursor: pointer;
  background-color: rgb(66, 160, 248);
}
